tion: none;\n  visibility: hidden;\n}\n#hero {\n  position: relative; /* Menjadikan kontainer hero posisi relatif */\n  width: 100%;\n  /* Tambahkan properti lain jika diperlukan */\n}\n\n#hero > picture > img {\n  width: 100%; /* Menyesuaikan lebar gambar dengan kontainer */\n  height: 500px; /* Membuat tinggi gambar otomatis sesuai rasio aslinya */\n  object-fit: cover; /* Memastikan gambar terisi sepenuhnya di dalam kotak tanpa merubah rasio aslinya */\n  /* Jika ingin mengatur objek untuk menutupi area tanpa memperhatikan rasio aslinya */\n  /* object-fit: fill; */\n}\n\n#hero > #hero-text {\n  position: absolute; /* Menjadikan elemen hero-text posisi absolut */\n  top: 50%; /* Menggeser elemen ke tengah secara vertikal */\n  left: 50%; /* Menggeser elemen ke tengah secara horizontal */\n  transform: translate(-50%, -50%); /* Mengatur ulang posisi elemen agar tepat di tengah */\n  text-align: center;\n  color: white;\n  font-size: 24px;\n  text-shadow: 2px 2px 4px #000000;\n  padding-left: 10%;\n  padding-right: 10%;\n\n  button:hover {\n    background-color: #ffc96d;\n  }\n\n  button {\n    background-color: $yellow;\n    min-height: 44px;\n    min-width: 44px;\n    padding: 0 10px;\n    border: none;\n    border-radius: 5px;\n    cursor: pointer;\n    color: white;\n  }\n\n  a {\n    color: white;\n    text-decoration: none;\n  }\n}\n\n\n#link {\n  margin-right: -30%;\n}\n\n#link li a:hover {\n  color: $yellow;\n}\n\n#main-nav{\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 100;\n  background-color: rgba(255, 255, 255, 0.7);\n  backdrop-filter: blur(10px);\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  transition: background-color 0.3s;\n  padding: 5px 50px;\n  > ul {\n    display: flex;\n    gap: 20px;\n\n    > li {\n      list-style: none;\n      min-height: 44px;\n      > a {\n        color: black;\n        text-decoration: none;\n        font-size: 24px;\n        \n      }\n    }\n  }\n}\n\n\n/* header *