n  margin-top: -20px;\n}\n.restaurant-name a{\n  color: #000000;\n  text-decoration: none;\n  display: inline-block;\n  min-height: 44px;\n}\nfooter {\n  height: 100px;\n  background-color: $yellow;\n  color: white;\n  text-align: center;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n/* Detail */\n\n#restaurant {\n  margin: 5%;\n  img {\n    width: 100%;\n  }\n}\n\n.menu-wrapper {\n  display: grid;\n  grid-template-columns: repeat(2 , 1fr); \n  gap: 20px;\n  justify-content: center;\n  place-items: center;\n}\n\n.card-menu {\n  min-height: 300px;\n  min-width: 280px;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  padding: 20px;\n  border-radius: 10px;\n  background-color: $yellow;\n  box-shadow: 14px 16px 24px -10px rgba(38,38,38,0.52);\n  -webkit-box-shadow: 14px 16px 24px -10px rgba(38,38,38,0.52);\n  -moz-box-shadow: 14px 16px 24px -10px rgba(38,38,38,0.52);\n  .menu-list{\n    text-align: center;\n    color: white;\n  }\n}\n\n.review-container {\n  width: 80%;\n  margin-top: 20px;\n  padding: 10px;\n  border: 1px solid #eee;\n  border-radius: 5px;\n  background-color: #fff;\n}\n\n.user-review {\n  margin-bottom: 20px;\n}\n\n.caption-review {\n  font-weight: bold;\n}\ninput[type=\"text\"] {\n  height: 44px;\n  width: 100%; \n  padding: 8px;\n  margin-bottom: 10px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  box-sizing: border-box; \n}\nbutton {\n  padding: 10px 20px;\n  background-color: $yellow;\n  color: #fff;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  font-size: 16px;\n}\n\nbutton:hover {\n  background-color: #be7c08;\n}\n\n\n.restaurant-reviews {\n  margin-top: 20px;\n  width: 100%;\n}\n\n\n.customer-review {\n  margin-bottom: 20px;\n  padding: 10px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  background-color: #f9f9f9;\n}\n\n.customer-review b {\n  font-weight: bold;\n}\n\n\n.customer-review p:nth-child(2) {\n  font-style: italic;\n  color: #888;\n  margin-bottom: 